export default {
	data() {
		return {
			listData: []
		}
	},
	onLoad() {
		uni.request({
			url: this.$Url + '/api/school/compress',
			method: 'GET',
			data: {},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					console.log(res)
					var arr = []
					for (let i in res.data.msg) {
						res.data.msg[i].image = this.$Url + res.data.msg[i].image
						res.data.msg[i].title = res.data.msg[i].title.length > 24 ? res.data.msg[i].title.substring(0, 24) + '...' :
							res.data.msg[i].title
						arr.push(res.data.msg[i]); //属性
					}
					this.listData = arr
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}
			}
		});
	},
	methods: {

	}
}
